@import "./styles/base";
@import "./styles/typography";
@import "./styles/utilities";
@import "./styles/components";

:root {
  //----------FONT-----------
  --font-default: 1.6rem;

  //----------APP-COLORS-------------

  --color-white: #fff;
  --color-white-dark: #f7f7f7;
  --color-white-dark-rgb: 247, 247, 247;
  --color-black: #000000;
  --color-black-light: #363636;
  --color-black-light-rgb: 54, 54, 54;
  --color-grey: #777777;
  --color-grey-light: #babecc;
  --color-grey-rgb: 119, 119, 119;

  //-------THEME-COLORS--------------

  --color-theme-light: #e9e8de;
  --color-theme-dark: #333;

  --color-item-theme-dark: #444444;
  --color-item-theme-light: #f7f6ea;

  --color-theme-form-light: #dddccc;
  --color-theme-form-dark: #1a1a1a;

  --color-theme-nav-bg-light: #d1d0c9;
  --color-theme-nav-bg-dark: #565656;

  //-------ALERT-WINDOWS-COLORS-----------

  --color-alert-success: rgba(7, 149, 66, 0.6);
  --color-border-alert-success: rgba(36, 241, 6, 0.46);
  --color-text-alert-success: #25ff0b;
  --color-hover-alert-success: rgba(7, 149, 66, 0.8);
  --color-shadow-success: #259c08;

  --color-alert-error: rgba(220, 17, 1, 0.5);
  --color-border-alert-error: rgba(236, 58, 58, 0.46);
  --color-text-alert-error: #ff0000;
  --color-hover-alert-error: rgba(255, 0, 0, 0.6);
  --color-shadow-error: #9c0808;

  //-----------BG-TIMING-ANIMATION-------------
  --time-bg-anim-default: 1s;
}
